

.container-input {
  text-align: center;
  background: #282828;
  border-top: 5px solid #c39f77;
  padding: 50px 0;
  border-radius: 6px;
  width: 50%;
  margin: 0 auto;
  margin-bottom: 20px;
}

.inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.inputfile + label {
  max-width: 20rem;
  font-size: 1.25rem;
  font-weight: 700;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  padding: 0.625rem 1.25rem;
}

.inputfile + label svg {
  width: 1em;
  height: 1em;
  vertical-align: middle;
  fill: currentColor;
  margin-top: -0.25em;
  margin-right: 0.25em;
}

.iborrainputfile {
  font-size: 16px;
  font-weight: normal;
  font-family: "Lato";
}

/* style 1 */

.inputfile-1 + label {
  color: #fff;
  background-color: #4099ff;
}

.inputfile-1:focus + label,
.inputfile-1.has-focus + label,
.inputfile-1 + label:hover {
  background-color: #40a9ff;
}

.selected {
  color: blue;
  font-weight: bold;
}

.navlink {
  color: gray;
  margin-left: 5px;
}

.navlink:hover {
  color: black;
}

.container {
  /* background-color: blue; */
  margin-top: 28px;
  width: 100%;
  padding: 0;
  border: 1px solid #d1811b;
}

.header {
  width: 100%;
  height: 65px;
  display: flex;
  align-items: center;
  /* background-color: white; */
}

.header a {
  margin-left: 15px;
}

.header img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.post-photo {
  width: 100%;
  height: auto;
}

.post-photo img {
  width: 100%;
  height: auto;
}

.likes {
  padding: 10px;
  width: 100%;
}

.likes-count {
  margin-left: 10px;
}

.comment-post-user {
  padding-left: 15px;
}

.post__comments {
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
  padding: 10px 20px 10px 20px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  grid-row-gap: 5px;
  row-gap: 5px;
}
.comment__form {
  margin: 0px 20px 20px 20px;
}
.comment__wrapper {
  display: flex;
  width: inherit;
  background-color: #fafafa;
  border-radius: 8px;
  border: 1px solid lightgray;
  padding: 8px;
}
.comment__wrapper input {
  width: 70%;
  border: none;
  background: transparent;
}
.comment__wrapper input:focus {
  border: none;
}
.comment__wrapper button {
  width: 30%;
  text-align: center;
}

.carrusel {
  width: 100%;
  /* background-color: pink; */
  /* height: 120px; */
  margin-top: 15px;
  display: flex;
  align-items: center;
  border: 1px solid #d1811b;
}

.image-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 4px;
}

.image-container img {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  border: 2px solid #6bd125;
  padding: 3px;
  margin: 0;
}

/* ------------------------------ */

.carrusel .glider-contain {
  /* background-color: blueviolet; */
  width: 90%;
}

.glider-track .glider-slide {
  min-width: 107px;
}

.container-userchange {
  /* background-color: blueviolet; */
  height: 107px;
  margin-top: 15px;
  width: 300px;
  display: flex;
  align-items: center;
  margin-left: 30px;
}

.image-container-userchange img {
  height: 65px;
  width: 65px;
  border-radius: 50%;
  border: 1px solid gray;
  padding: 2px;
}

.username-container {
  /* background-color: yellow; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: 10px;
  line-height: 18px;
}

.username-gray {
  color: gray;
  opacity: 0.5;
}

.change-user {
  /* background-color: blue; */
  width: 50%;
  display: flex;
  justify-content: end;
  font-weight: bold;
}

.home-container {
  height: 100vh;
  width: 100vw;
  padding-top: 70px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.post {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  width: 50%;
  height: 100%;
  /* background-color: aqua; */
  overflow-y: auto;
}

.post::-webkit-scrollbar {
  display: none;
}

.app {
  height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: column;
  background: #fafafa;
}

html,
body {
  padding: 0;
  margin: 0;
}

link {
  text-decoration: none;
}

